/* Calculator.css */

/* Wrapper overrides or custom styling */
.calculator-container {
    /* You can further refine your container here */
    background-color: #fff;
    border-radius: 0.5rem;
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.1);
    margin: 0 auto;
    max-width: 30rem; /* ~480px */
    padding: 1.5rem;
  }
  
  /* Title overrides or custom styling */
  .calculator-title {
    color: #0088FF;
    font-size: 1.5rem; /* ~text-2xl */
    font-weight: 600;
    text-align: center;
    margin-bottom: 1rem;
  }
  
  /* Label styling */
  .calculator-label {
    display: block;
    font-weight: 500;
    margin-bottom: 0.25rem;
  }
  
  /* Input styling */
  .calculator-input {
    width: 100%;
    height: 2.25rem; /* ~h-9 */
    padding: 0.5rem;
    border: 1px solid #ccc;
    border-radius: 0.25rem;
  }
  
  /* Button styling */
  .calculator-button {
    width: 100%;
    height: 2.5rem; /* ~h-10 */
    margin-top: 1.5rem;
    background-color: #0088FF;
    color: #fff;
    border: none;
    border-radius: 0.25rem;
    cursor: pointer;
  }
  
  .calculator-button:hover {
    background-color: #0066CC;
  }
  
  .calculator-button:disabled {
    opacity: 0.7;
    cursor: default;
  }
  